          12: 'The moon can’t shine on her own, but even so, she’s nice enough to share the light someone else gives her. She gives us the chance to walk in the darkness, and it’s on us to take or leave that opportunity.',
          13: 'Today I may cry like never before. Today perhaps not only will my strings be cut, but my soul itself too. Today I may want that. I’m done with this foolishness.',
