          15: 'I then noticed that there wasn’t cold wood around me anymore. There were stars. Little stellar fragments dancing around me, watching after me, whispering to me all those sweet words my mother gave me during all this time.',
          16: 'I laughed. My hands and walls were a disaster, all covered in scotch tape and glue.',
          17: 'I hurried a bit, so I may have accidentally ruined some notes along the way, and maybe later I’ll have to clean up this mess.',
          18: 'But, this is it. Definitively. What I needed. This perspective of time, and of what it really means to hang on to hope…It brings me an unavoidable smile, brings me unstoppable inspiration!',
          19: 'Then I prepared and ran through the stellar fields. I ran through time and space, making sure to imprint on my heart each and every single one of the fragments that were following me, telling me to continue in my race!',
          20: 'There was something incredibly beautiful in watching with my own eyes so many emotions safeguarded and accumulated in such a small place; a lonely and tiny speck in the middle of the endless universe, so condensed that it seemed like it would explode in a new Big Bang. It had a familiar shape, and once I saw it, I decided…',
          21: 'Even though I can’t shine by myself…I’ll start by protecting the light that protects me. The shine of the moon.',
          22: 'Maybe I’ll have to fool others, even myself, with that shine. But eventually…with all certainty…I’ll shine by myself.',
          23: 'I have the power to create a new universe. That power is right here, searching for me, and I’m looking for her. When we encountered each other, she was surprised to see me, as though something had scared me…and yes, I am afraid, but I gathered all the will that had grown in me and materialized it into a smile.',
          24: 'I looked up at the moon, and said to her…',
          25: '“Thank you for everything. Today I want to really try.”',
          26: 'Maybe I’ve given off the impression of not having left my home for a long time. That’s not the case; I go regularly to the academy, after all. But I’m always looking down, afraid of making eye contact with anyone, following a mechanical routine. A calculated, robotic one.',
          27: 'But today, after so many years, I managed to look up.',
          28: 'The company of the moon, and no one else, made me gain a little bit more  confidence. I felt free and bathed in her light. We shared a mutual hug, after a time that felt eternal to me, that lasted a true eternity.',
          29: 'My dreams are awakened by the darkness instead of the light.'
