          51: 'I can’t just go and ask: “Would you cut me in little pieces if I made a mistake that angers you very much?”',
          52: 'What makes me think',
          53: 'I',
          54: 'wouldn’t cut a person in little pieces too? Maybe a relationship is like a leap of faith. Nobody knows what will happen, but we do the best we can so it doesn’t end up wrong.',
          55: 'Being afraid of people…Is it the same as not having faith in them?'
